import * as React from 'react';
import { FormattedMessage } from 'react-intl';
import Link from '~src/_i18n/components/Link';
import useIntlHelper from '~src/_i18n/helpers/useIntlHelper';
//import { Post } from '~src/types/Post';
import PostCard from '../PostCard/PostCard';
import * as styles from './ReadNext.module.scss';

export const ReadNext = ({ data }) => {
  const relatedPosts = data.relatedPosts;
  const primaryTag = data.post.frontmatter.tags[0];
  const { formatMessage, intl } = useIntlHelper();
  return (
    <aside className={styles.readNext}>
      <div className={`inner `}>
        <div className={styles.readNextFeed}>
          <article className={styles.readNextCard}>
            <header className={styles.readNextCardHeader}>
              <h3>
                <span>{formatMessage('posts.moreByTag')}</span>{' '}
                <Link to={`/tags/${primaryTag}/`}>{formatMessage('tags.' + primaryTag + '.title')}</Link>
              </h3>
            </header>
            <div className={styles.readNextCardContent}>
              <ul>
                {relatedPosts.nodes.map(post => (
                  <li key={post.id}>
                    <h4>
                      <a href="/welcome/">{post.frontmatter.title}</a>
                    </h4>
                    <div className={styles.readNextCardMeta}>
                      <p>
                        <time dateTime={post.frontmatter.date}>
                          {intl.formatDate(post.frontmatter.date, {
                            year: 'numeric',
                            month: 'short',
                            day: 'numeric',
                          })}
                        </time>{' '}
                        – {intl.formatNumber(post.timeToRead) + ' '}
                        {formatMessage('posts.timeToRead')}
                      </p>
                    </div>
                  </li>
                ))}
              </ul>
            </div>
            <footer className={styles.readNextCardFooter}>
              <a href={`/tags/${primaryTag}`}>
                <FormattedMessage id="posts.seeAll" values={{ num: relatedPosts.totalCount }} />
              </a>
            </footer>
          </article>
          {relatedPosts.nodes.map((post: Post_Type) => (
            <PostCard isHome={false} post={post} i={0} key={post.id} />
          ))}
        </div>
      </div>
    </aside>
  );
};

export default ReadNext;
